        Title: 'In pictures: ISIS training snipers for attacks in Yemen',
        Brief: 'ISIS is training sniper units in a military camp set up in Yemenâ€™s Al Bayda province',
        Text:
          'The Islamic State (ISIS) is training sniper units in a military camp set up in Yemenâ€™s Al Bayda province. Pictures show nine graduates of a sniper training programm in Qifah area, equipped with sniper rifles and ghillie suits.\n\nAl Bayda, though mostly controlled by Houthis and Yemenâ€™s Sanaâ€™a based Popular Committee, has recently seen an increase of Al Qaeda and ISIS led aussaults against the Yemenite forces. Images of previous attacks indicate that especially ISIS uses a widespread underground network for itâ€™s operations in the region.\n\nWhile the Houthis together with the Yemenite military face off against the Saudi led coalition along Yemenâ€™s north-western border to Saudi Arabia, an reportedly lost the Republican Palace in Yemenâ€™s southern province Taiz to the coalition, the perpetual attacks in the Al Bayda Governorate in central Yemen also threaten the south-eastern borders of the Popular Committeeâ€™s territory.',
